import { adapters } from "@rp/adapters"
import { useHashParams } from "@rp/react/web/common/hooks"
import { IconButton } from "@rp/react/web/mui/core"
import { IosShare } from "@rp/react/web/mui/icons"

const ShareDocument = () => {
  const [documentParams] = useHashParams()
  const { document: documentId = "" } = documentParams

  const handleShare = () => {
    window.navigator.share({
      url: `${adapters.location.getRelativeBaseUrl()}files/#?document=${documentId}`,
    })
  }

  return (
    <IconButton onClick={handleShare}>
      <IosShare />
    </IconButton>
  )
}

export { ShareDocument }
